.main-btn {
  position: relative;
  display: inline-block;
  min-width: 264px;
  padding: 20px;
  color: #171921;
  text-align: center;
  font-family: "SF Pro Display", "Arial", "Helvetica Neue", "Helvetica", sans-serif;
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
  border-radius: 40px;
  transition: all 0.3s ease 0s;
  overflow: hidden;
  background: linear-gradient(280deg, #111520 -80.16%, #DDB06D 70.75%); }
  .main-btn::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(280deg, #111520 -80.16%, #DDB06D 70.75%);
    transition: all 0.3s ease 0s; }
  .main-btn:hover::before {
    transform: rotate(-180deg); }
  .main-btn span {
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-color: transparent;
    padding: 13px 20px;
    border: none; }
  .main-btn a {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    opacity: 0; }

.text {
